:root
{
    --DownloadThumbnailLoader : 0.4 ;
}

@media (orientation:Landscape ){
    .downloaderPage
    {
        /*background: red;*/
        width: 100vw;
        /*background: red;*/
        display: grid;
        place-items: center;
    }
    .downloaderPagecontentsection
    {
        display: grid;
        grid-template-columns: 0.3fr 0.7fr;
        justify-content: center;
        align-items: center;
        gap: 40px;
        flex-wrap: wrap;
        width: 95vw;
    }
    .downloaderPageThumbnail img
    {
        height: 250px;
        width: 450px;
        border-radius: 20px;
    }
    .downloaderPageThumbnail
    {
        padding: 20px;
    }
    .downloaderPageThumbnail:before
    {
        content: "";
        position: absolute;
        height: 250px;
        width: 450px;
        background: black url("images/white/loader.gif");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        opacity: var(--DownloadThumbnailLoader);
        border-radius: 20px;
    }
    .downloaderPageDownloadBTN
    {
        border: 0px solid black;
        border-radius: 20px;
        padding: 10px 50px;
        font-size: 20px;
        font-family: var(--contentFONT);
        letter-spacing: 1px;
        font-weight: bold;
        background: whitesmoke;
        height: 60px;
        width: 200px;
        transition: background 0.2s;
    }
    .downloaderPageContent
    {
        display: grid;
        gap: 20px;
    }
    .downloaderPageThanksLetter
    {
        width: 100vw;
        display: grid;
        /*padding: 10px 0px;*/
    }
    .downloaderPageThanksLetter
    {
        /*background: blue;*/
        width: 75vw;
        padding-top: 50px;
    }
}

