.downloaderPageDownloadBTN
{
    display: flex;
}
.downloaderPageDownload
{
    display: flex;
    height: 0;
    overflow: hidden;
    transition: height 0.5s;
    gap: 20px;
}
.downloaderPageDownloadBTN:active
{
    background: #e5e4e4;
}
.downloaderPageDownloadBTN img
{
    height: 100px;
    background: red;
}




/*  LOADING */

@keyframes ldio-cgueqoct9h {
    0% { transform: translate(-50%,-50%) rotate(0deg); }
    100% { transform: translate(-50%,-50%) rotate(360deg); }
}
.ldio-cgueqoct9h
{
    transition: opacity 0.2s;
    opacity: 0;
}
.opacity1
{
    opacity: 1;
}
.ldio-cgueqoct9h div {
    position: absolute;
    width: 15px;
    height: 15px;
    border: 3px solid #0a0a0a;
    border-top-color: transparent;
    border-radius: 50%;
}
.ldio-cgueqoct9h div {
    animation: ldio-cgueqoct9h 1s linear infinite;
    top: 20px;
    left: 20px
}
.loadingio-spinner-rolling-kdy5hjmhgt8 {
    width: 50px;
    height: 50px;
    display: inline-block;
    overflow: hidden;
    background: #f1f2f3;
}
.ldio-cgueqoct9h {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0; /* see note above */
}
.ldio-cgueqoct9h div { box-sizing: content-box; }