@media (orientation: Landscape) {
    .Beat_Menu
    {
        /*background: red;*/
        display: grid;
        place-items: center;
    }
    .Beat_Logo img
    {
        height: 40px;
        padding-left: 15px;
        padding-right: 40px;
    }
    .Beat_Search_Bar
    {
        height: 30px;
        width : 250px;
        border: 0;
        border-radius: 20px;
        box-shadow: 0px 0px 8px lightgray;
    }
    .YGBeats_label
    {
        display: grid;
        place-items: center;
    }
    .Beat_List
    {
        padding: 50px;
        display: flex;
        gap: 90px;
        justify-content: center;
        flex-wrap: wrap;
        transition: opacity 0.5s;
    }
    .Beat_Card
    {
        border-radius: 20px;
        display: block;
        height: 185px;
        width: 360px;
    }
    .beat_Thumbnail
    {
        border-radius: 20px;
        width: 400px;
        position: absolute;
    }
    .Beat_Written_Content
    {
        position: absolute;
        background: linear-gradient(transparent,black);
        border-radius: 20px;
        color: white;
        font-family: var(--contentFONT);
        padding: 20px;
        width: 360px;
        height: 185px;
        display: grid;
        align-items: end;
        grid-template-columns: auto 1fr;
    }
    .Beat_Title
    {

    }
    .Beat_download
    {
        justify-self: end;
        opacity: 0;
        /*transition: opacity 0.1s;*/
    }
    .Beat_Card:hover .Beat_Written_Content  .Beat_download
    {
        opacity: 1;
    }
    .Beat_download img
    {
        height: 20px;
        width: 20px;
    }
    .Beat_Downloader_Display
    {
        opacity: 1;
    }
}

