*{
    padding: 0;
    margin: 0;
}
@media (orientation: Landscape) {
    .ProjectsMainPage
    {
        display: grid;
        justify-content: center;
    }
    .ProjectsMainPage h2
    {
        margin-bottom: 100px;
    }
    .projectsarea
    {
        display: flex;
        gap: 50px;
        flex-wrap: wrap;
    }
    .projectcard
    {
        height: 250px;
        width: 400px;
        background: white;
        box-shadow: 0px 0px 10px #e5e5e5;
        border-radius: 20px;
        display: grid;
        grid-template-rows: 0.25fr 0.75fr;
        padding: 20px;
        gap: 20px;
    }
    .UpperProjectCard
    {
        background: transparent;
        display: grid;
        grid-template-columns: 0.75fr 0.25fr;
        place-items: center;
    }
    .LowerProjectCard
    {
        background: transparent;
        display: grid;
        place-items: center;
    }
    .ProjectCardTitle
    {
        background: transparent;
        font-family: var(--contentFONT);
        color: var(--maintitlecolor);
    }
    .ProjectCardIcon
    {
        background: whitesmoke;
        border-radius: 20px;
    }
    .ProjectCardIcon .P_icon
    {
        height: 50px;
    }
    .ProjectCardDisp
    {
        font-size: 17px;
        letter-spacing: 0.2px;
        font-family: var(--contentFONT);
        color: grey;
    }
}
